.burgerBtn {
  border: none;
  background-color: #fff;
  display: none;
  width: 28px;
  height: 28px;
  position: relative;
  z-index: 1;

  &::before,
  &::after {
    content: '';
    background-color: #000;
    position: absolute;
    height: 3px;
    left: 0;
    right: 0;
    transition: transform 0.3s, top 0.3s 0.3s;
  }

  &::before {
    top: 5px;
  }

  &::after {
    top: calc(100% - 7px);
  }

  div {
    background-color: #000;
    height: 3px;
    transition: height 0.3s 0.3s;
  }

  &.active {

    div {
      height: 0;
    }

    &::before,
    &::after {
      top: 50%;
      transition: top 0.3s, transform 0.3s 0.3s;
    }

    &::before {
      transform: translateY(-50%) rotate(45deg);
    }

    &::after {
      transform: translateY(-50%) rotate(-45deg);
    }
  }
}


.burgerContainer {
  background-color: rgba(0,0,0,0.2);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transform: translateX(-100%);
  transition: transform 0.3s 0.3s;
  overflow: hidden;
  z-index: 2;

  nav {
    background-color: #fff;
    padding: 30px;
    width: max-content;
    position: absolute;
    right: 0;
    top: 130px;
    transform: translateX(100%);
    transition: transform 0.3s;

    ul {
      margin: -20px 0;
    }

    li {
      margin: 20px 0;
    }

    a {
      color: inherit;
      transition: opacity 0.3s;

      &:hover,
      &:focus {
        opacity: 0.5;
      }
    }
  }

  &.active {
    transform: translateX(0);
    transition: transform 0.3s;

    nav {
      display: block;
      transform: translateX(0);
      transition: transform 0.3s 0.3s;
    }
  }
}