@font-face {
  font-display: swap;
  font-family: 'Mont';
  font-style: normal;
  font-weight: 800;
  src: url('/assets/src/fonts/Mont-Black.woff') format('woff'), /* Modern Browsers */
  url('/assets/src/fonts/Mont-Black.ttf') format('truetype'); /* Safari, Android, iOS */
}

