button:not(.cursor) {
  cursor: pointer;
}

.blockBtn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;

  color: #fff;
  background-color: #FF5029;

  font-family: inherit;
  font-weight: 600;
  font-size: 18px;
  line-height: lh(18,30);

  border: none;
  border-radius: 12px;
  padding: 20px 40px;

  text-align: center;
  transition: color 0.3s, background-color 0.3s;

  &:hover,
  &:focus {
    background-color: rgba(255,80,41,0.8);
  }
}


@mixin blockBtn-992 {

  .blockBtn {
    padding: 15px 30px;
  }

}


@mixin blockBtn-768 {

  .blockBtn {
    padding: 10px 20px;
    font-size: 16px;
  }

}


@mixin blockBtn-576 {

  .blockBtn {

  }

}