.guides {
  margin-bottom: calc( var(--indDesc) * 8.6 );
}

.guides__wrapper {
  padding: 0 60px;
}

.guides__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: calc( var(--indBox) * 4.7 );
}

.guides__body {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: -14px;
}

.guides__card {
  color: inherit;
  background-color: #fff;
  border-radius: 20px;
  display: block;
  width: 289px;
  margin: 14px;
  padding: 40px 48px;
  box-shadow: 0 30px 60px 0 rgba(198, 191, 191, 0.15),
  0 -8px 20px 0 rgba(235, 235, 235, 0.15);
  transition: background-color 0.3s;
  
  &:hover,
  &:focus {
    background-color: #FF5029;

    .guides__cardName,
    .guides__cardNote {
      color: #fff;
    }

    .guides__cardRating {

      svg {
        fill: #fff;
      }
    }
  }
}

.guides__cardImg {
  background-color: #D9D9D9;
  border: 2px solid #FF5029;
  border-radius: 50%;
  height: 188px;
  width: 188px;
  overflow: hidden;
  margin-bottom: 30px;
}

.guides__cardName {
  font-size: 24px;
  font-weight: 700;
  line-height: lh(24,32);
  text-align: center;
  margin-bottom: 12px;
  transition: color 0.3s;
}

.guides__cardRating {
  text-align: center;
  margin-bottom: 8px;

  svg {
    fill: #FF5029;
    transition: fill 0.3s;
  }
}

.guides__cardNote {
  color: rgba(11, 6, 20, 0.60);
  line-height: lh(16,22);
  text-align: center;
  transition: color 0.3s;
}


@mixin guides-768 {

  .guides__wrapper {
    padding: 0;
  }

}


@mixin guides-576 {

  .guides__header {
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    margin-top: -10px;
  }

  .guides__title {
    margin: 10px;
  }

  .guides__btn {
    margin: 10px;
  }

  .guides__body {
    justify-content: center;
    margin: -10px 0;
  }

  .guides__card {
    margin: 10px 0;
    width: 275px;
  }

  .guides__cardImg {
    height: 150px;
    width: 150px;
    margin-left: auto;
    margin-right: auto;
  }

  .guides__cardName {
    font-size: 22px;
  }

}