.footer {
  background-color: #0B0614;
  line-height: lh(16,28);
  color: #fff;
}

.footer__body {
  padding: 90px 60px 40px;
  overflow: hidden;
}

.footer__items {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -74px 40px;
}

.footer__item {
  margin: 20px 74px;
}

.footer__logo {
  font-size: 36px;
  line-height: lh(36,40);
  margin-bottom: 30px;
}

.footer__text {
  opacity: 0.8;
  max-width: 333px;
}

.footer__itemTitle {
  font-size: 20px;
  font-weight: 700;
  line-height: lh(20,28);
  margin-top: 9px;
  margin-bottom: 30px;
}

.footer__itemList {

  li {
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: inherit;
    opacity: 0.8;
    transition: opacity 0.3s;

    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}

.footer__itemContacts {
  margin-bottom: 30px;
}

.footer__itemBlockFlex {
  display: flex;
  align-items: center;
  color: inherit;
  margin-bottom: 12px;
  transition: opacity 0.3s;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:last-child {
    margin-bottom: 0;
  }

  svg {
    margin-right: 12px;
    stroke: #FF5029;
    height: 24px;
    width: 24px;
  }

}

.footer__separate {
  height: 1px;
  background-color: rgba(255, 255, 255, 0.15);
  margin-bottom: 30px;
}

.footer__note {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: -10px -20px;
}

.footer__year {
  opacity: 0.8;
  margin: 10px 20px;

  span {
    color: #FF5029;
  }
}

.footer__policy {
  display: flex;
  flex-wrap: wrap;

  a {
    color: inherit;
    margin: 10px 20px;
    opacity: 0.8;
    transition: opacity 0.3s;

    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}


@mixin footer-992 {

  .footer__item {

    &:first-child {
      width: 100%;
    }
  }

}


@mixin footer-576 {

  .footer__body {
    padding: 45px 0 20px;
  }

}