.howWork {
  margin-bottom: calc( var(--indDesc) * 8.6 );
}

.howWork__wrapper {
  padding: 0 60px;
}

.howWork__title {
  text-align: center;
  margin-bottom: calc( var(--indBox) * 5 );
}

.howWork__body {
  display: flex;
  flex-wrap: wrap;
  margin: -16px;
}

.howWork__card {
  background-color: #FEF4F5;
  border-radius: 20px;
  width: 392px;
  margin: 16px;
  padding: 45px;
}

.howWork__cardImg {
  width: 175px;
  margin: 0 auto 45px;
}

.howWork__cardTitle {
  font-size: 40px;
  line-height: lh(40,50);
  text-align: center;
}


@mixin howWork-768 {

  .howWork__wrapper {
    padding: 0;
  }

}

@mixin howWork-576 {

  .howWork__body {
    justify-content: center;
  }

  .howWork__card {
    padding: calc( var(--indDesc) * 3.3 );
  }

  .howWork__cardImg {
    width: calc( var(--indDesc) * 13 );
    margin-bottom: calc( var(--indDesc) * 3.3 );
  }

  .howWork__cardTitle {
    font-size: 5.2vw;
  }

}