@import "../fonts.scss";

:root {
  --indBox: calc(1% + 1vh);
  --indDesc: calc(1vw + 1vh);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: Mont, Arial, sans-serif;
  color: #0B0614;

  a:not(.outline),
  input:not(.outline),
  textarea:not(.outline),
  button:not(.outline) {
    outline: none;
  }

  a:not(.textDecoration) {
    text-decoration: none;
  }

  ul:not(.listStyle) {
    list-style: none;
  }

  img {
    object-fit: cover;
    max-width: 100%;
  }
}

.container {
  margin: 0 auto;
  max-width: 1400px;
  padding: 0 20px;
}

.myMain {
  flex-grow: 1;
}