.blockTitle--1 {
  font-size: 110px;
  line-height: lh(110,120);
}


@mixin blockTitle1-992 {

  .blockTitle--1 {
    font-size: 72px;
  }

}


@mixin blockTitle1-768 {

  .blockTitle--1 {
    font-size: 55px;
  }

}


@mixin blockTitle1-576 {

  .blockTitle--1 {
    font-size: 7.4vw;
  }

}
