.twoLayers__bg {
  border-radius: 24px;
  position: relative;
  overflow: hidden;
  height: 740px;

  img {
    position: absolute;
    inset: 0;
    height: 100%;
  }
}

.twoLayers__bgBack {
  z-index: -1;
}

.twoLayers__title {
  max-width: 940px;
  margin: 0 auto;
  text-align: center;
}


@mixin twoLayers-992 {

  .twoLayers__bg {
    height: 525px;
  }

  .twoLayers__title {
    max-width: 610px;
  }

}


@mixin twoLayers-768 {

  .twoLayers__bg {
    height: 405px;
  }

  .twoLayers__title {
    max-width: 470px;
  }

}


@mixin twoLayers-576 {

  .twoLayers__bg {
    height: 53vw;
  }

  .twoLayers__title {
    max-width: 63vw;
  }

}