.blockTitle--2 {
  font-size: 64px;
  line-height: lh(64,70);
}

@mixin blockTitle2-992 {

  .blockTitle--2 {
    font-size: 52px;
  }

}

@mixin blockTitle2-768 {

  .blockTitle--2 {
    font-size: 40px;
  }

}

@mixin blockTitle2-576 {

  .blockTitle--2 {
    font-size: 6.5vw;
  }

}