.socials {

  ul {
    display: flex;
  }

  li {
    margin-right: 8px;

    &:last-child {
      margin-right: 0;
    }
  }

  a {
    border: 1px solid #FF5029;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    transition: border-color 0.3s;

    &:hover,
    &:focus {
      border-color: #fff;

      svg {
        fill: #fff;
      }
    }
  }

  svg {
    fill: #FF5029;
    transition: fill 0.3s;
    height: 20px;
    width: 20px;
  }
}