.welcome {
  position: relative;
  margin-bottom: calc( var(--indBox)  * 10 );
}

.welcome__bg {
  border-radius: 24px;
  padding-top: 125px;
  position: relative;
  overflow: hidden;
  height: 740px;

  img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
  }
}

.welcome__bgBack {
  z-index: -1;
}

.welcome__title {
  max-width: 940px;
  margin: 0 auto;
  text-align: center;
}


@mixin welcome-992 {

  .welcome__bg {
    height: 525px;
    padding-top: 93px;
  }

  .welcome__title {
    max-width: 610px;
  }

}


@mixin welcome-768 {

  .welcome__bg {
    height: 405px;
    padding-top: 72px;
  }

  .welcome__title {
    max-width: 470px;
  }

}


@mixin welcome-576 {

  .welcome__bg {
    height: 53vw;
    padding-top: 8.7vw;
  }

  .welcome__title {
    max-width: 63vw;
  }

}