.reference {
  position: relative;
  margin-bottom: calc( var(--indBox)  * 8.6 );
}

.reference__bg {
  padding-top: 167px;
}


@mixin reference-992 {

  .reference__bg {
    padding-top: 133px;
  }

}


@mixin reference-768 {

  .reference__bg {
    padding-top: 105px;
  }

}


@mixin reference-576 {

  .reference__bg {
    padding-top: 12.7vw;
  }

}