.about {
  margin-bottom: calc( var(--indDesc) * 8.6 );
}

.about__body {
  padding: 0 60px;
}

.about__title {
  text-align: center;
  margin-bottom: calc( var(--indBox) * 4.7 );;
}

.about__cards {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 303px);
}

.about__card {
  display: flex;
  color: inherit;
  border-radius: 20px;
  background-color: #fff;
  box-shadow: 0 30px 60px 0 rgba(198, 191, 191, 0.15),
  0 -8px 20px 0 rgba(235, 235, 235, 0.15);
  padding: 15px;
  transition: background-color 0.3s;

  &:hover,
  &:focus {
    background-color: #FF5029;

    .about__cardText {
      color: #fff;
    }
  }

  &:nth-child(1) {
    grid-row-start: 1;
    grid-row-end: 3;
    flex-direction: column;

    .about__cardImg {
      height: 367px;
    }

    .about__cardWrapper {
      align-self: flex-start;
    }
  }

  &:nth-child(2),
  &:nth-child(3) {

    .about__cardImg {
      width: 181px;
    }
  }
}

.about__cardImg {
  background-color: #E9A1A1;
  border-radius: 20px;
  flex-shrink: 0;
  overflow: hidden;
  margin: 15px;
}

.about__cardWrapper {
  align-self: center;
  margin: 15px;
}

.about__cardTitle {
  display: inline-block;
  color: #FF5029;
  background-color: #FEF4F5;
  font-size: 24px;
  padding: 8px 20px;
  margin-bottom: 24px;
  border-radius: 12px;
}

.about__cardText {
  font-size: 40px;
  line-height: lh(40,52);
  width: 82%;
  transition: color 0.3s;
}


@mixin about-1400 {

  .about__cards {
    grid-template-columns: minmax(1px, 605px);
    grid-template-rows: auto;
    justify-content: center;
  }

  .about__card {
    height: 303px;

    &:nth-child(1) {
      grid-row-start: auto;
      grid-row-end: auto;
      flex-direction: row;

      .about__cardImg {
        height: auto;
        width: 181px;
      }

      .about__cardWrapper {
        align-self: center;
      }
    }
  }

  .about__cardText {
    font-size: 30px;
  }

}


@mixin about-768 {

  .about__body {
    padding: 0;
  }

  .about__cardTitle {
    font-size: 20px;
  }

}


@mixin about-576 {

  .about__cards {
    grid-gap: 10px;
  }

  .about__card {
    height: auto;
    padding: 10px;

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {

      .about__cardImg {
        height: 40vw;
        width: 30vw;
      }
    }
  }

  .about__cardImg {
    margin: 5px;
  }

  .about__cardWrapper {
    margin: 5px;
  }

  .about__cardTitle {
    font-size: 4vw;
    margin-bottom: 15px;
  }

  .about__cardText {
    font-size: 5vw;
  }

}