$margin-right-1: 24px;

.header {
  font-size: 16px;
}

.header__body {
  align-items: center;
  display: flex;
  padding: 1.88em 4.42%;
}

.header__logo {
  margin-right: $margin-right-1;
}

.header__separate {
  flex-shrink: 0;
  background: rgba(11, 6, 20, 0.30);
  margin-right: $margin-right-1;
  height: 2em;
  width: 2px;
}

.header__search {
  align-items: center;
  display: flex;
  flex-grow: 1;
  margin-right: $margin-right-1;
  min-width: 190px;

  button {
    flex-shrink: 0;
    background: url(/assets/src/img/common/search.svg) no-repeat center;
    border: none;
    margin-right: 8px;
    height: 24px;
    width: 24px;
  }

  input {
    border: 1px solid transparent;
    border-radius: 5px;
    flex-grow: 1;
    color: inherit;
    font: inherit;
    padding: 4px;
    transition: border-color 0.3s;

    @include inputPlaceholder( inherit, rgb(11, 6, 20), 0.3 );

    &:hover,
    &:focus {
      border-color: #FF5029;
    }
  }
}

.header__menu {
  margin-right: 40px;

  ul {
    display: flex;
    margin: 0 -20px;
  }

  li {
    margin: 0 20px;
  }

  a {
    color: inherit;
    transition: opacity 0.3s;

    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }
}

.header__btn {
  font-size: 17px;
  flex-shrink: 0;
  padding: 20px 30px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25),
    1px 0 1px #000000, -1px 0 1px #000000, 0 1px 1px #000000, 0 -1px 1px #000000;
  transition: background-color 0.3s;
}


@import 'assets/src/scss/styles/common/blocks/burger';



@mixin header-1200 {

  .header__body {
    padding-left: 0;
    padding-right: 0;
  }

  .header__menu {
    display: none;
  }

  .header__btn {
    margin-right: $margin-right-1;
  }

  .header__burgerBtn {
    display: block;
  }

}


@mixin header-768 {

  .header__body {
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .header__separate {
    display: none;
  }

  .header__search {
    margin-top: 20px;
    margin-right: 0;
    order: 1;
    width: 100%;
  }

}


@mixin header-576 {

  .header {
    font-size: 14px;
  }

  .header__btn {
    padding: 10px 20px;
  }

}

@mixin header-576 {

  .header {
    font-size: 14px;
  }

  .header__btn {
    padding: 10px 20px;
  }

}


@mixin header-375 {

  .header__btn {
    order: 2;
    margin-right: 0;
    margin-left: auto;
    margin-top: 20px;
    width: 100%;
  }

}