.search {
  font-size: 20px;
  position: absolute;
  top: 675px;
  width: 100%;
}

.search__body {
  background-color: #fff;
  box-shadow: 0 30px 60px 0 rgba(198, 191, 191, 0.18);
  border-radius: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 60px;
  padding: 24px;
}

.search__items {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  margin: -12px 20px -12px -8px;
}

.search__item {
  border-right: 1px solid rgba(11,6,20,0.2);
  margin: 12px 0;
  padding: 0 24px;
  width: 100%;

  &:last-child {
    border-right: none;
  }
}

.search__itemLabel {
  background-color: #fff;
  opacity: 0.5;
  font-size: 0.8em;
  margin-bottom: 8px;
}

.search__itemList {
  margin-top: 8px;
  position: relative;

  select {
    appearance: none;
    border: 0;
    background-color: transparent;
    font: inherit;
    cursor: pointer;
    width: 100%;
    outline: none;

    &::-ms-expand {
      display: none;
    }
  }

  &::after {
    content: url(/assets/src/img/common/arrow-down.svg);
    position: absolute;
    top: 50%;
    right: 0;
    pointer-events: none;
    transform: translateY(-50%);
    transition: transform 0.3s;
    height: 24px;
    width: 24px;
  }

  &.active {

    &::after {
      transform: translateY(-50%) rotate(180deg);
    }
  }

}

.search__btn {
  font: inherit;
  padding: 26px 31px;
}


@mixin search-1400 {

  .search {
    right: 0;
    width: auto;
  }

  .search__items {
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  .search__item {
    max-width: 260px;

    &:nth-child(2) {
      border-right: none;
    }
  }

}


@mixin search-992 {

  .search {
    margin-top: 20px;
    position: static;
  }

  .search__body {
    margin: 0;
  }

  .search__items {
    margin-right: 0;
    margin-left: 0;
  }

  .search__item {
    border-right: none;
    width: 190px;
  }

  .search__btn {
    padding: 18px 23px;
  }

}


@mixin search-768 {

  .search {
    font-size: 17px;
  }

  .search__items {
    margin-top: -8px;
    margin-bottom: -8px;
  }

  .search__item {
    padding-right: 10px;
    padding-left: 10px;
    margin-top: 8px;
    margin-bottom: 8px;
    width: 140px;
  }

  .search__btn {
    padding: 15px 20px;
  }

}


@mixin search-576 {

  .search__body {
    flex-direction: column;
  }

  .search__items {
    flex-direction: column;
    margin-bottom: 20px;
  }

  .search__item {
    width: 73vw;
  }

}

