.referForm {
  margin-top: -110px;
  position: relative;
}

.referForm__body {
  background-color: #fff;
  box-shadow: 0 30px 60px 0 rgba(198, 191, 191, 0.18);
  border-radius: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 60px;
  padding: 25px;
}

.referForm__text {
  font-size: 40px;
  line-height: lh(40,54);
  margin: 15px;

  span:last-child {
    color: #FF5029;
  }
}

.referForm__firstBlock {
  flex-shrink: 0;
  margin: 15px;
}

.referForm__btn {
  margin-bottom: 30px;
}

.referForm__secondBlock {
  display: flex;
  align-items: center;
  margin: -8px;
}

.referForm__imgs {
  display: flex;
  margin: 8px;

  img {
    border: 2px solid #FFF;
    border-radius: 50%;
    height: 52px;
    width: 52px;
    margin-left: -22px;

    &:first-child {
      margin-left: 0;
    }
  }

}

.referForm__note {
  color: rgba(11, 6, 20, 0.60);
  font-size: 16px;
  line-height: lh(16,22);
  width: 103px;
  margin: 8px;

  span:first-child {
    color: #FF5029;
  }
}


@mixin referForm-992 {

  .referForm {
    position: static;
    margin-top: 20px;
  }

  .referForm__body {
    flex-direction: column;
    align-items: flex-start;
    margin: 0;
  }

  .referForm__firstBlock {
    align-self: flex-end;
  }

}


@mixin referForm-768 {

  .referForm__text {
    font-size: 35px;
  }

}


@mixin referForm-576 {

  .referForm__body {
    padding: 10px;
  }

  .referForm__text {
    font-size: 5.5vw;
  }

  .referForm__secondBlock {
    flex-wrap: wrap;
  }

}